.greeting {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.greeting-text {
  display: block;
  justify-content: center;
  min-height: 50vh;
}

.greeting-text .big-name {
  color: var(--primary);
  font-size: 5rem;
  font-weight: bold;
}

.greeting-text .role-title {
  color: var(--text);
  font-size: 2.5rem;
}

#typewrite {
  color: var(--text);
  font-size: 2.5rem;
}

.greeting-text p {
  color: var(--text);
  font-size: 1.5rem;
}

.greeting .button-container {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  gap: 3vw;
}

.greeting img {
  flex-shrink: 1;
  width: 70vh;
  height: 80vh;
}

.project {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 90vh;
}

.project-text {
  max-width: 40vw;
}

.project .button-container {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  gap: 3vw;
}

.project h1 {
  color: var(--primary);
}

.project p {
  color: var(--text);
}

.button {
  border-radius: 0.3rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
.button p {
  text-align: center;
  font-size: 1.5rem;
  margin: 0;
}
.button a {
  text-decoration: none;
  color: var(--background);
  margin: 0;
}

.small-button {
  padding: 0.3rem 0.6rem;
  border-radius: 0.75rem;
}
.small-button p {
  font-size: 1.2rem;
}

.primary-button {
  background-color: var(--primary);
}

.secondary-button {
  background-color: var(--secondary);
}
.secondary-button a {
  color: var(--text);
}

.accent-button {
  background-color: var(--accent);
}

.navbar {
  width: 90vw;
  height: 9vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--secondary);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5vw;
  padding-right: 5vw;
}
.navbar a {
  color: var(--text);
  cursor: pointer;
  text-decoration: none;
  margin: 0;
}
.navbar p {
  color: var(--text);
  cursor: pointer;
  text-decoration: none;
  margin: 0;
}
.navbar .navbar-name {
  font-family: "Courgette", cursive;
  font-size: 2rem;
}

.navbar .section-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5vw;
}
.navbar .section-links a:hover {
  text-decoration: solid underline 1px;
  text-underline-offset: 2px;
}

#toggle-color-scheme {
  cursor: pointer;
  position: relative;
  width: 4rem;
  height: 2rem;
}

#toggle-shaft {
  position: absolute;
  height: 2rem;
  width: 4rem;
  border-radius: 1rem;
  background-color: var(--background);
}

.toggle-knob-forward {
  right: 0;
}

#toggle-knob {
  position: absolute;
  height: 2rem;
  width: 2rem;
  border-radius: 1rem;
  background-color: var(--accent);
}

body, html, :root {
  --background: #f0f0f0;
  --primary: #b2b2b3;
  --secondary: #e3e3e3;
  --accent: #7e7d7c;
  --text: #0f0f0f;
  font-family: "Source Code Pro", monospace;
  font-size: 16px;
  margin: 0;
  padding: 20px;
  scroll-behavior: smooth;
}

.light-theme {
  background-color: #f0f0f0;
  --background: #f0f0f0;
  --primary: #b2b2b3;
  --secondary: #e3e3e3;
  --accent: #7e7d7c;
  --text: #0f0f0f;
}

.dark-theme {
  background-color: #030104;
  --background: #030104;
  --primary: #a1d883;
  --secondary: #0a1f0d;
  --accent: #a7ba3b;
  --text: #f2e8f8;
}

.section {
  height: 91vh;
}

.section-gap {
  height: 9vh;
}